@import 'styles/variables.scss';

.loginPage {
  form {
    display: flex;
    flex-direction: column;
    padding: 0 48px 42px;
    align-items: center;
    width: 355px;
    margin: 90px auto 0;
    position: relative;
    background: #fff;
    border: 1px solid #d6d6d6;
    border-radius: 16px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  }

  .logo {
    svg {
      width: 193px;
    }
  }

  :global {
    .MuiTextField-root {
      margin-bottom: 16px;

      &.hasError {
        .MuiFormHelperText-root {
          color: $errorColor;
          margin-left: 0;
        }
      }
    }
  }

  button {
    width: 100%;
  }
}
